  width: 100%;
  margin: 0;
}
.intro .countdown .days,
.intro .countdown .hours,
.intro .countdown .minutes,
.intro .countdown .seconds {
  font-size: 36px;
  line-height: 15px;
  color: #3d3d3d;
  font-weight: 600;
