  border-radius: 10px;
  box-shadow: 1px 1px 6px 0px rgba(160, 160, 160, 0.6);
}
.intro .outbox .grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  -moz-column-gap: 25px;
       column-gap: 25px;
  row-gap: 25px;
}
@media (max-width: 768px) {
  .intro .outbox .grid {
    grid-template-columns: 1fr;
  }
}
.intro .outbox h3 {
  font-size: 28px;
  line-height: 29px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 20px;
}
.intro .outbox .outboxCard {
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.05);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  border: 1px solid #008454;
  padding: 0 15px 14px 15px;
}
.intro .outbox .outboxCard label {
  padding: 12px 16px 11px;
