  font-size: 15px;
  line-height: 28px;
  text-align: center;
  margin: 6px 0 15px 0;
}
.intro .outbox .outboxCard button {
  font-size: 18px;
  font-weight: 600;
  line-height: 35px;
  display: inline-flex;
  background-size: 35px;
  background-repeat: no-repeat;
  background-position: 5px 5px;
  padding: 5px 20px 5px 50px;
