}
.intro .pricingPlans .plan label {
  display: block;
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  background-color: #f8f8f8;
  padding: 15px;
  border-bottom: 1px solid #e7e7e7;
