}
.intro .pricingPlans .plan .priceAre {
  color: #444;
  justify-content: center;
  margin-bottom: 30px;
}
.intro .pricingPlans .plan .priceAre .currency {
  font-size: 38px;
  font-weight: 700;
}
.intro .pricingPlans .plan .priceAre .number {
  font-size: 70px;
  font-weight: 700;
}
.intro .pricingPlans .plan .priceAre .decimal {
  font-size: 18px;
  font-weight: 700;
  display: block;
}
.intro .pricingPlans .plan .priceAre .period {
  font-size: 15px;
}
.intro .pricingPlans .plan .info {
  text-align: center;
}
.intro .pricingPlans .plan .info .billed {
  text-transform: uppercase;
  color: #555;
  margin-bottom: 10px;
}
.intro .pricingPlans .plan .info .sites {
  font-weight: 500;
  color: #2DA1D0;
  margin-bottom: 40px;
}
.intro .pricingPlans .plan .info .support {
  color: #333;
  margin-bottom: 45px;
}
.intro .pricingPlans .plan .updateButton {
  font-size: 26px;
  font-weight: 700;
  line-height: 120%;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
  width: calc(100% - 30px);
  color: #000;
  background-color: #E0E0E0;
  padding: 15px;
  border-bottom: 4px solid #9f9f9f;
  border-radius: 8px;
  margin-bottom: 30px;
