            padding: 0;

            >span {
                font-size: 36px;
                line-height: 25px;
                color: #3d3d3d;
                font-weight: 600;
            }

            div {
                display: inline-block;
                text-align: center;
                width: 100%;
                margin: 0;
            }

            .days,
            .hours,
            .minutes,
            .seconds {
                font-size: 36px;
                line-height: 15px;
                color: #3d3d3d;
                font-weight: 600;
                display: block;
            }

            .days_ref,
            .hours_ref,
            .minutes_ref,
            .seconds_ref {
                line-height: 20px;
                font-size: 12px;
                opacity: 0.4;
            }
        }

        .promoCode {
            text-align: center;
            font-size: 26px;
            font-weight: 700;
        }
    }